.s-partners {
    padding: rem(77) 0 rem(102);

    .title {
        text-align: center;
        margin-bottom: rem(30);

        @media (min-width: 768px) {
            margin-bottom: rem(63);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(90);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(120);
        }
    }

    // &__items {
    //     display: flex;
    //     flex-wrap: wrap;
    // }

    &__items {
        position: relative;
        display: grid;
        align-items: center;
        justify-content: center;
        max-width: rem(520);
        margin: 0 auto;
        grid-template-columns: repeat(2, 1fr);
        gap: 0 rem(50);

        @media (min-width: 768px) {
            gap: 0 rem(120);
        }

        @media (min-width: 1600px) {
            display: flex;
            align-items: center;
            max-width: 100%;
            gap: 0;

            .s-partners__btn-wrap {
                display: none;
            }
        }

        &::before {
            @media (max-width: 1599px) {
                position: absolute;
                top: 50%;
                left: 50%;
                width: rem(1);
                height: 100%;
                background: #424346;
                transform: translate(-50%, -50%);
                gap: 0 rem(120);
                content: "";
            }
        }

        .s-partners__item {
            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3),
            &:nth-child(4) {
                border-bottom: rem(1) solid $grey_dark;
            }
        }
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        width: rem(134);
        // width: 20%;
        min-height: rem(81);
        margin: 0 auto;
        padding: rem(17) rem(14);
        opacity: 0.3;
        @include tr("all");

        @media (min-width: 768px) {
            width: rem(200);
            min-height: rem(200);
            padding: rem(20) rem(14);
        }

        @media (min-width: 1600px) {
            width: 20%;
            min-height: rem(160);
            border-bottom: 0 !important;
            border-left: rem(1) solid $grey_dark;

            &:first-child {
                border-left: 0;
            }
        }

        // &:nth-of-type(5n + 1) {
        //     border-left: none;
        // }

        // &:first-child {
        //     border-left: none;
        // }

        img {
            display: block;
            width: 100%;
            height: auto;
            max-height: rem(73);

            @media (min-width: 768px) {
                max-height: rem(78);
            }

            @media (min-width: 1600px) {
                max-width: rem(150);
            }
        }

        &:hover {
            color: $white;
            opacity: 1;

            img {
                filter: brightness(0) invert(1);
            }
        }
    }

    &__btn {
        font-size: rem(12);
        font-weight: 500;
        line-height: 1.2;
        color: #424346;
        letter-spacing: 0.2em;
        white-space: nowrap;

        @media (min-width: 768px) {
            font-size: rem(15);
        }
    }

    .text-center {
        text-align: center;
    }
}
