* {
    box-sizing: border-box;
}

input,
button,
textarea {
    -webkit-appearance: none;
}

html,
body {
    min-width: 320px;
    color: $white;
    background: $black;
    overflow-x: hidden !important;

    &.open {
        overflow-y: hidden;
    }

    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10000;
        width: 100%;
        height: 100%;
        background: $black;
    }

    &.preloaded {
        &:after {
            display: none;
        }
    }
}

body {
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 100%;
    min-height: 100vh;
    min-width: 320px;
    font-family: $primary_font;
    font-size: 16px;
    line-height: 1.2;
    color: $grey_light_more;
    background: linear-gradient(
        357.79deg,
        #202020 1.85%,
        rgba(40, 46, 66, 0) 69.85%
    );
    font-style: normal;
    letter-spacing: normal;
    flex-direction: column;
}
.catalog-page {
    // background: url(/img/catalog-page/back.png);
    background-position: top;
    background-repeat: no-repeat;
    background-size: contain;
}

.main {
    z-index: z("l-default");
    flex: 1 auto;

    * {
        scrollbar-width: thin;
        scrollbar-color: #3a3b44;
    }

    /* Works on Chrome, Edge, and Safari */
    *::-webkit-scrollbar {
        cursor: pointer;
        width: 4px;
        height: 4px;

        @media (min-width: 1600px) {
            width: 8px;
            height: 8px;
        }
    }

    *::-webkit-scrollbar-track {
        background: #000203;
    }

    *::-webkit-scrollbar-thumb {
        border-radius: 12px;
        background-color: #3a3b44;
    }
}

.lazy,
.swiper-lazy {
    opacity: 0;
}

.loading,
.loaded,
[data-was-processed="true"],
[data-ll-status="entered"],
.swiper-lazy-loaded {
    opacity: 1;
    @include tr(all);
}

.hidden {
    display: none !important;
    opacity: 0 !important;
}

.show,
.show_btn {
    display: flex !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
}

textarea,
a,
button,
input[type="submit"] {
    text-decoration: none;
    color: inherit;
    outline: none;
    @include tr(all);
    &:disabled {
        opacity: 0.4;
    }
}

a,
button,
input[type="submit"] {
    cursor: pointer;
}

input[type="submit"]:disabled {
    cursor: not-allowed;
    opacity: 0.5;
}

button {
    background: none;
    border: none;
}

input,
video {
    border: none;
    outline: none;
}

.fancybox-content {
    padding: 0;
}

input,
textarea {
    &::placeholder {
        @include tr("all");
    }
    &:focus {
        &::placeholder {
            opacity: 0 !important;
        }
    }
}

img {
    display: block;
    max-width: 100%;
}

.title {
    margin: 0;
    padding: 0;
    line-height: 100%;

    &--h1 {
        font-size: 16px;
        line-height: 1.2;
        opacity: 0.4;
        letter-spacing: 0.64em;

        @media (min-width: 992px) {
            font-size: 37px;
            font-weight: 300;
            line-height: 1.2;
        }
    }

    &--h2 {
        font-size: rem(19);
        font-weight: 600;
        line-height: rem(22);
        color: $grey_light_more;

        @media (min-width: 768px) {
            font-size: 32px;
            font-weight: 900;
            line-height: 1.2;
        }

        @media (min-width: 992px) {
            font-size: 40px;
        }
    }

    &--h3 {
        font-size: 32px;
        font-weight: normal;
        line-height: 1.2;

        @media (min-width: 992px) {
            font-size: 152px;
        }
    }
}

.l {
    &-default {
        z-index: z("l-default");
        position: relative;
        width: 100%;
        max-width: 1920px;
        margin: 0 auto;
        padding: 0 70px;
        box-sizing: border-box;

        @media screen and (max-width: 1023px) {
            padding: 0 15px;
        }
    }
    &-content {
        z-index: z("l-default");
        position: relative;
        width: 100%;
        max-width: 1440px;
        margin: 0 auto;
        // padding: 0 16px;
        box-sizing: border-box;

        @media screen and (min-width: 1299px) {
            // padding: 0 70px;
        }
        @media screen and (min-width: 1600px) {
            padding: 0;
        }
    }
}

a,
button {
    &[disabled],
    &[disabled="disabled"] {
        pointer-events: none;
    }
}

.btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: fit-content;
    font-size: 12px;
    line-height: 14px;
    transition: 0.5s;
    @include tr("all");

    &--search {
        padding: 4px 18px;
        color: #000203;
        background: $black_light;
        border-radius: 8px;

        &:hover,
        &:active {
            color: #ababb2;
            background: $grey_dark;
        }
    }

    &--bordered {
        color: $white;
        border: 1px solid $grey_white;
        border-radius: 7px;

        &:hover,
        &:active {
            border: 1px solid $red;
        }
    }

    &--grey {
        color: $white;
        background: $grey_dark;
        border-radius: 5px;

        &:hover,
        &:active {
            background: $black;
        }
    }

    &--black {
        color: $white;
        background: $black_light;
        border-radius: 5px;

        &:hover,
        &:active {
            background: $black;
        }
    }
}

.close {
    position: relative;
    width: 15px;
    height: 15px;

    span {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 1px;
        background: $grey_light_more;

        &:first-child {
            transform: translate(-50%, -50%) rotate(45deg);
        }

        &:last-child {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }
}

.shadow {
    z-index: z("shadow");
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100vh;
    background: #000;
    opacity: 0.5;
}

input,
textarea {
    background: #f7f7f7;
    border: none;
}

.categories-partners {
    margin-top: 103px;
    .title {
        font-size: 38px;
        text-align: center;
    }
    .partners {
        .title {
            display: none;
        }
    }
}

.fancybox-content {
    z-index: 99994;
    background: none;
}

.overflow-y-hidden {
    overflow-y: hidden !important;
}

.fancybox-slide--html .fancybox-close-small {
    top: 15px;
    right: 15px;
    padding: 0;
}

.hide {
    opacity: 0;
}

.show-hide {
    opacity: 1;
    animation: show 1.5s ease;
}

@keyframes show {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes showFromTop {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
