.s-about-graviton {
    position: relative;
    width: 100%;
    overflow: visible;
    padding: rem(60) 0 rem(136);

    @media (min-width: 768px) {
        padding: rem(115) 0;
    }

    .title {
        position: relative;
        margin-bottom: rem(45);
        text-align: center;
        color: #d9d9d91a;
        opacity: 0.6;
        letter-spacing: rem(26);

        @media (min-width: 768px) {
            text-align: left;
            letter-spacing: rem(16);
            font-size: rem(70);
        }

        @media (min-width: 1200px) {
            letter-spacing: rem(90);
            font-size: rem(110);
        }
    }

    &__text {
        position: relative;
        z-index: 1;
        max-width: rem(704);
        margin-top: rem(300);
        margin-left: rem(20);
        margin-bottom: rem(50);

        @media (min-width: 768px) {
            margin-top: 0;
            margin-bottom: rem(250);
            margin-left: auto;
        }

        p {
            margin-bottom: rem(20);
            font-size: rem(14);
            font-weight: 400;
            line-height: 1.5;

            @media (min-width: 768px) {
                font-size: rem(20);
                font-weight: 500;
                line-height: 1.8;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__items {
        position: relative;
        z-index: 1;
    }

    &__item {
        position: relative;
        font-size: rem(12);
        font-weight: 300;
        line-height: 1.2;

        @media (min-width: 768px) {
            font-size: rem(20);
            font-weight: 500;
        }

        &:first-child {
            margin-bottom: rem(40);
            margin-left: rem(20);
            padding-left: rem(20);

            @media (min-width: 768px) {
                max-width: rem(714);
                margin-right: rem(150);
                margin-bottom: rem(47);
                margin-left: rem(120);
                padding-left: 0;
            }

            @media (min-width: 992px) {
                margin-left: auto;
            }

            &:after {
                content: "";
                position: absolute;
                bottom: rem(-15);
                left: 0;
                width: rem(220);
                height: rem(28);
                padding-bottom: rem(23);
                padding-left: rem(40);
                border-bottom: rem(1) solid #8d8d8d;
                border-left: rem(1) solid #8d8d8d;

                @media (min-width: 768px) {
                    bottom: rem(-40);
                    left: rem(-40);
                }
            }

            &:before {
                content: "";
                position: absolute;
                z-index: 1;
                bottom: rem(-22);
                left: rem(-4);
                width: rem(10);
                height: rem(10);
                background: $red;
                border-radius: 100%;

                @media (min-width: 768px) {
                    bottom: rem(-44);
                    left: rem(-44);
                }
            }
        }

        &:last-child {
            margin-bottom: rem(40);
            margin-left: rem(20);
            padding-left: rem(20);

            @media (min-width: 768px) {
                max-width: rem(714);
                margin-top: rem(140);
                margin-left: rem(250);
                padding-left: 0;
            }

            &:after {
                content: "";
                position: absolute;
                bottom: rem(-15);
                left: 0;
                width: rem(220);
                height: rem(28);
                padding-bottom: rem(23);
                padding-left: rem(40);
                border-bottom: rem(1) solid #8d8d8d;
                border-left: rem(1) solid #8d8d8d;

                @media (min-width: 768px) {
                    bottom: rem(-40);
                    left: rem(-40);
                }
            }

            &:before {
                content: "";
                position: absolute;
                z-index: 1;
                bottom: rem(-22);
                left: rem(-4);
                width: rem(10);
                height: rem(10);
                background: $red;
                border-radius: 100%;

                @media (min-width: 768px) {
                    bottom: rem(-44);
                    left: rem(-44);
                }
            }
        }
    }
}
