.s-contact-us {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    position: fixed;
    z-index: 99;
    right: 0;
    bottom: rem(35);
    width: auto;

    @media (min-width: 768px) {
        bottom: rem(40);
    }

    @media (min-width: 1200px) {
        bottom: rem(50);
    }

    .close {
        position: absolute;
        top: rem(12);
        right: rem(12);
        cursor: pointer;

        &:hover {
            opacity: 0.6;
        }
    }

    &__left {
        display: flex;
        flex: 0 0 rem(32);
        align-items: center;
        justify-content: center;
        min-width: 0;
        margin-top: rem(-80);

        @media screen and (max-width: 600px) {
            flex-direction: column;
        }

        &-text {
            @include tr("all");
            padding: rem(7) rem(10);
            transform: rotate(-90deg);
            cursor: pointer;
            font-size: rem(15);
            color: $grey_white;
            background: $grey;
            white-space: nowrap;
            border-top-left-radius: rem(5);
            border-top-right-radius: rem(5);

            @media screen and (max-width: 600px) {
                display: none;
            }

            &:hover {
                background: $black_light;
            }
        }

        &-icon, &-top {
            display: none;

            @media screen and (max-width: 600px) {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 47px;
                height: 37px;
                font-size: 18px;
                line-height: 1;
                background: $black_light;
                border-radius: 7px;
                margin-bottom: 11px;
            }
        }
    }

    &__content {
        @include tr("all");
        display: none;
        position: relative;
        max-width: rem(242);
        padding: rem(30) rem(40);
        animation: show 0.4s ease-in-out;
        color: $white;
        background: rgba(78, 80, 86, 0.95);
        border-radius: rem(5);
        opacity: 0;
        border-top-left-radius: 0;

        @media screen and (max-width: 600px) {
            padding: 20px;
        }
    }

    &__item {
        margin-bottom: rem(10);
        padding-bottom: rem(17);

        &:not(:last-child) {
            border-bottom: rem(1) solid rgba(189, 190, 197, 0.6);
        }

        &-phone {
            font-size: rem(15);
            font-weight: 600;
            line-height: 1.2;
            color: $grey_light_more;
        }

        &-btn {
            padding: rem(6) rem(14);
            margin-top: rem(6);
            font-size: rem(12);
            line-height: 1;
        }

        &-title {
            font-size: rem(13);
            line-height: 1.2;
            color: $grey_light_more_2;
        }
    }

    &.active {
        .s-contact-us {
            &__left {
                margin-top: rem(60);
                &-text {
                    background: $red;
                }
            }

            &__content {
                display: block;
                opacity: 1;
            }
        }
    }
}
