.s-characteristics {
    position: relative;
    overflow: hidden;

    @media (min-width: 1200px) {
        min-height: rem(1841);
    }

    @media (max-width: 600px) {
        padding-bottom: 140px;
    }

    .l-default {
        max-width: rem(1300);
        padding: 0;
    }

    &__img {
        position: absolute;
        z-index: 1;
        top: -2%;
        left: 50%;
        width: rem(1158);
        min-height: rem(1030);
        transform: translateX(-50%);
        opacity: 0.5;

        @media (min-width: 1200px) {
            top: -20%;
            min-height: rem(1600);
        }

        @media (min-width: 1200px) {
            top: -20%;
            min-height: rem(2150);
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: flex-start;
        position: relative;
        z-index: 2;
        padding: 0 15px;
        margin-top: rem(91);

        @media (min-width: 1200px) {
            flex-direction: row;
            justify-content: space-between;
            padding: 0;
        }
    }

    &__item {
        width: 100%;

        @media (min-width: 1200px) {
            width: 50%;
        }

        &-icon {
            font-size: rem(35);
            line-height: 1;

            @media (min-width: 1200px) {
                font-size: rem(55);
            }
        }

        &-text {
            font-size: 15px;
            line-height: 120%;
        }

        &-top {
            position: relative;
            margin-bottom: rem(5);
            padding-bottom: rem(25);
            padding-left: rem(24);

            @media (min-width: 1200px) {
                min-height: rem(105);
                margin-bottom: rem(50);
                padding-bottom: rem(33);
                padding-left: 0;
            }

            &:after {
                content: "";
                position: absolute;
                bottom: rem(10);
                left: 0;
                width: rem(222);
                height: rem(28);
                padding-bottom: rem(23);
                padding-left: rem(40);
                border-bottom: rem(1) solid #8d8d8d;
                border-left: rem(1) solid #8d8d8d;

                @media (min-width: 1200px) {
                    bottom: 0;
                    left: rem(-40);
                    width: 100%;
                    height: rem(28);
                    padding-bottom: rem(23);
                    padding-left: rem(40);
                    border-left: 0;
                    border-right: rem(1) solid #8d8d8d;
                }
            }
        }

        &-number {
            max-width: fit-content;
            border: rem(3.5) solid $grey_dark;
            padding: rem(6);
            margin-top: rem(8);
            margin-right: rem(15);
            font-size: rem(34);
            font-weight: 500;
            line-height: 1;
            color: $grey_dark;
            white-space: nowrap;

            @media (min-width: 1200px) {
                font-size: rem(40);
            }
        }

        &-title {
            font-size: rem(28);
            font-weight: 500;
            line-height: 1;
            color: $grey_dark;

            @media (min-width: 1200px) {
                font-size: rem(33);
            }
        }

        &:first-child {
            .s-characteristics {
                &__item {
                    &-top {
                        @media (min-width: 1200px) {
                            max-width: rem(276);
                        }

                        &:before {
                            content: "";
                            position: absolute;
                            z-index: 1;
                            bottom: rem(4);
                            left: rem(-4);
                            width: rem(10);
                            height: rem(10);
                            background: $red;
                            border-radius: 100%;

                            @media (min-width: 1200px) {
                                right: rem(-4);
                                bottom: rem(-4);
                                left: auto;
                            }
                        }
                    }

                    &-text {
                        @media (min-width: 1200px) {
                            max-width: rem(240);
                        }

                        p {
                            margin-top: 0;
                            margin-bottom: rem(20);
                            font-size: rem(12);

                            @media (min-width: 1200px) {
                                font-size: rem(15);
                            }
                        }
                    }

                    &-content {
                        position: relative;

                        @media (min-width: 1200px) {
                            max-width: rem(557);
                            min-height: rem(450);
                        }

                        &:after {
                            @media (min-width: 1200px) {
                                content: "";
                                position: absolute;
                                right: rem(151);
                                bottom: rem(-64);
                                width: rem(1);
                                height: 100%;
                                transform: rotate(-35deg);
                                background: #8d8d8d;
                            }
                        }
                    }
                }
            }
        }

        &:nth-child(2) {
            margin-top: rem(60);

            @media (min-width: 1200px) {
                margin-top: rem(400);
            }

            .s-characteristics {
                &__item {
                    &-top {
                        display: flex;
                        align-items: flex-start;
                        max-width: rem(278);
                        margin-right: rem(-43);
                        margin-left: auto;

                        @media (max-width: 600px) {
                            padding-left: 0;
                        }

                        @media (min-width: 1200px) {
                            margin-right: rem(61);
                        }

                        &:before {
                            content: "";
                            position: absolute;
                            z-index: 1;
                            bottom: rem(4);
                            left: rem(-44);
                            width: rem(10);
                            height: rem(10);
                            background: $red;
                            border-radius: 100%;

                            @media (min-width: 1200px) {
                                bottom: rem(-4);
                            }
                        }

                        &:after {
                            content: "";
                            position: absolute;
                            left: rem(-40);
                            padding-right: rem(40);
                            padding-bottom: rem(23);
                            padding-left: 0;
                            border-bottom: rem(1) solid #8d8d8d;
                            border-left: rem(1) solid #8d8d8d;
                            border-right: none;
                        }
                    }

                    &-text {
                        max-width: rem(278);
                        margin-left: auto;
                        font-size: rem(12);

                        @media (min-width: 1200px) {
                            max-width: rem(340);
                            font-size: rem(15);
                        }

                        p {
                            margin-top: 0;
                            margin-bottom: rem(20);
                        }
                    }

                    &-title {
                        max-width: rem(140);
                    }

                    &-content {
                        position: relative;
                        max-width: rem(557);
                        margin-left: auto;

                        @media (min-width: 1200px) {
                            min-height: rem(427);
                        }

                        &:after {
                            @media (min-width: 1200px) {
                                content: "";
                                position: absolute;
                                bottom: rem(92);
                                left: rem(58);
                                width: rem(1);
                                height: 100%;
                                transform: rotate(35deg);
                                background: #8d8d8d;
                            }
                        }
                    }
                }
            }
        }

        &:nth-child(3) {
            margin-top: rem(60);

            @media (min-width: 1200px) {
                transform: translateY(-250px);
            }

            .s-characteristics {
                &__item {
                    &-top {
                        display: flex;
                        align-items: flex-start;
                        max-width: rem(348);
                        @media (max-width: 600px) {
                            padding-left: 40px;
                        }
                        &:before {
                            content: "";
                            position: absolute;
                            z-index: 1;
                            bottom: rem(4);
                            left: rem(4);
                            width: rem(10);
                            height: rem(10);
                            background: $red;
                            border-radius: 100%;

                            @media (min-width: 1200px) {
                                right: rem(-4);
                                bottom: rem(-4);
                                left: auto;
                            }

                            @media (max-width: 600px) {
                                left: rem(6);
                            }
                        }

                        &:after {
                            right: 75px;
                            left: unset;
                            max-width: rem(276);
                            margin-top: rem(75);

                            @media (min-width: 1200px) {
                                right: 0;
                            }

                            @media (max-width: 600px) {
                                left: 12px;
                            }
                        }
                    }

                    &-text {
                        max-width: rem(326);
                        margin-left: rem(5);
                        font-size: rem(12);

                        @media (min-width: 1200px) {
                            max-width: rem(340);
                            margin-left: rem(40);
                            font-size: rem(15);
                        }

                        p {
                            margin-top: 0;
                            margin-bottom: rem(20);
                        }
                    }

                    &-content {
                        position: relative;
                        max-width: rem(557);

                        @media (min-width: 1200px) {
                            min-height: rem(450);
                        }

                        &:after {
                            @media (min-width: 1200px) {
                                content: "";
                                position: absolute;
                                right: rem(80);
                                bottom: 90px;
                                width: rem(1);
                                height: 100%;
                                transform: rotate(-35deg);
                                background: #8d8d8d;
                            }
                        }
                    }
                }
            }
        }

        &:last-child {
            margin-top: rem(60);

            @media (min-width: 1200px) {
                margin-top: rem(200);
            }

            .s-characteristics {
                &__item {
                    &-top {
                        display: flex;
                        align-items: flex-start;
                        max-width: rem(276);
                        margin-right: rem(-5);
                        margin-left: rem(10);
                        padding-left: rem(50);

                        @media (min-width: 992px) {
                            margin-left: auto;
                            margin-right: rem(90);
                        }

                        @media (min-width: 1200px) {
                            margin-right: rem(63);
                            margin-left: auto;
                            padding-left: 0;
                        }

                        &:before {
                            content: "";
                            position: absolute;
                            z-index: 1;
                            bottom: rem(4);
                            left: rem(20);
                            width: rem(10);
                            height: rem(10);
                            background: $red;
                            border-radius: 100%;

                            @media (min-width: 1200px) {
                                bottom: rem(-4);
                                left: rem(-44);
                            }
                        }

                        &:after {
                            content: "";
                            position: absolute;
                            left: rem(25);
                            padding-right: rem(40);
                            padding-bottom: rem(23);
                            padding-left: 0;
                            border-bottom: rem(1) solid #8d8d8d;
                            border-left: rem(1) solid #8d8d8d;
                            border-right: none;

                            @media (min-width: 1200px) {
                                left: rem(-40);
                                padding-right: rem(40);
                            }
                        }
                    }

                    &-text {
                        max-width: rem(340);
                        margin-left: rem(30);
                        font-size: rem(12);

                        @media (min-width: 992px) {
                            margin-left: auto;
                        }

                        @media (min-width: 1200px) {
                            max-width: rem(340);
                            font-size: rem(15);
                        }

                        p {
                            margin-top: 0;
                            margin-bottom: rem(20);
                        }
                    }

                    &-title {
                        max-width: rem(215);
                    }

                    &-content {
                        position: relative;
                        max-width: rem(557);
                        min-height: rem(340);
                        margin-left: auto;

                        @media (min-width: 1200px) {
                            min-height: rem(427);
                        }

                        @media screen and (max-width: 600px) {
                            min-height: 0;
                        }

                        &:after {
                            @media (min-width: 1200px) {
                                content: "";
                                position: absolute;
                                bottom: rem(92);
                                left: rem(58);
                                width: rem(1);
                                height: 100%;
                                transform: rotate(35deg);
                                background: #8d8d8d;
                            }
                        }
                    }
                }
            }
        }
    }
}
