.header {
    position: relative;
    padding: rem(13) 0 0;

    @media screen and (max-width: 991px) {
        padding: rem(15) 0;
    }

    .l-default {
        position: static;
        overflow: visible;
    }

    &__wrapper {
        display: flex;
        align-items: center;
    }

    &__logo {
        display: flex;
        align-items: center;
        order: 0;
        width: auto;
        margin-right: rem(102);
        @media screen and (max-width: 991px) {
            justify-content: center;
            width: 100%;
            margin-right: rem(-65);
            margin-left: 0;
        }

        svg {
            display: block;
            width: auto;
        }

        &-emblem {
            margin-right: rem(9);

            @media screen and (max-width: 991px) {
                margin-right: 0;
            }

            svg {
                height: rem(30);
            }
        }

        &-text {
            svg {
                height: rem(12);
            }
        }

        @media screen and (max-width: 1449px) {
            font-size: rem(26);
            margin-right: rem(55);
        }

        .logo--text {
            @media screen and (max-width: 991px) {
                display: none;
            }
        }
    }

    &__phone {
        order: 2;
        text-align: right;
        color: $white;
        margin-left: auto;

        @media screen and (max-width: 991px) {
            display: none;
        }

        &-link {
            font-size: rem(21);
            line-height: 1;

            @media screen and (max-width: 1439px) {
                font-size: rem(19);
            }
        }

        &-text {
            font-size: rem(12);
            line-height: 1;
            color: $grey;
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        order: 9;
        position: relative;
        padding: rem(12) rem(35);
        margin-left: rem(50);

        @media screen and (max-width: 1439px) {
            margin-left: rem(30);
        }

        @media screen and (max-width: 991px) {
            max-width: 100%;
            border: none;
            padding: rem(23) rem(20);
            background: #0f1113;
            margin-left: 0;
        }

        &-text {
            margin: 0 auto;
            font-size: rem(12);
            line-height: 1;

            @media screen and (max-width: 991px) {
                font-size: rem(15);
                letter-spacing: 0.6em;
                color: $grey_light;
            }
        }

        &-icon {
            display: none;
            font-size: rem(15);
            line-height: 1;

            @media screen and (max-width: 991px) {
                display: block;
            }
        }

        &-arrow {
            display: none;
            font-size: rem(12);
            line-height: 1;

            @media screen and (max-width: 991px) {
                display: block;
            }
        }
    }

    &__menu {
        background: $black_2;
        margin-top: rem(40);

        @media screen and (max-width: 991px) {
            display: none;
        }

        &-wrapper {
            display: flex;
            align-items: center;
        }
    }

    &__burger {
        @include tr("all");
        display: none;
        position: relative;
        z-index: 2;
        min-width: rem(35);
        min-height: rem(35);
        margin-left: auto;

        @media screen and (max-width: 991px) {
            display: block;
        }

        span {
            position: absolute;
            height: rem(2);
            background: $grey_dark;

            &:first-child {
                top: rem(14);
                left: 0;
                width: 100%;
            }

            &:last-child {
                top: rem(24);
                right: 0;
                width: 50%;
            }
        }
    }

    &__mobile {
        display: none;
        flex-direction: column;
        position: fixed;
        z-index: 100;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        min-height: 100vh;
        transition: all 0.6s ease-in-out;
        color: #9a9ea9;
        background: $black;
        overflow-y: auto;
        overflow-x: hidden;
        padding-bottom: rem(15);

        @media screen and (max-width: 991px) {
            display: flex;
            transform: translateX(-100%);
            opacity: 0;
        }

        .menu {
            margin: 0;
            &__items {
                flex-direction: column;
            }
        }

        &-search {
            &.open {
                width: 100%;
            }
        }

        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: relative;
            min-height: rem(64);
            padding: rem(15);
            border-bottom: rem(1) solid $black_light;
        }

        .close {
            width: rem(22);
            height: rem(22);
        }

        &-btn {
            display: flex;
            align-self: flex-end;
            padding: rem(15) rem(33);
            margin-top: auto;
            margin-left: auto;
            margin-right: rem(20);
        }

        &.open {
            transform: translateX(0);
            opacity: 1;
        }

        &-title {
            display: none;

            @media screen and (max-width: 991px) {
                display: block;
                font-size: rem(17);
                line-height: 1;
                text-align: center;
            }
        }
    }
}
