.location {
    display: flex;
    align-items: center;
    @media screen and (max-width: 1439px) {
        margin-right: rem(50);
    }

    @media screen and (max-width: 991px) {
        display: none;
    }

    &-icon {
        min-width: rem(12);
        font-size: rem(12);
        line-height: 1;
        margin-right: rem(8);
    }

    &-text {
        font-size: rem(14);
        line-height: 1;
        color: $grey;

        @media screen and (max-width: 1439px) {
            font-size: rem(13);
        }
    }
}
