$white: #f4f4f2;
$black: #1e1e1d;

.main-top {
    padding-top: rem(72);
    background: url(".././img/main-top/bg.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;

    @media (max-width: 600px) {
        padding-top: 60px;
    }

    .title {
        text-align: center;
        color: $grey_white;

        span {
            opacity: 0;
            animation: fadeIn 20s alternate infinite;
        }
    }

    &__notebook {
        position: relative;
        margin-top: rem(50);
        
        &-img {
            position: relative;
            z-index: 3;
        }

        &-video {
            position: fixed;
            top: 0;
            left: 0;
            z-index: 10000;
            width: 100%;
            min-height: 100vh;
            opacity: 1;
            object-fit: cover;
            transform: translate(0);
        }

        &:before {
            content: "";
            position: absolute;
            z-index: 4;
            bottom: 0;
            left: 0;
            width: 30%;
            height: 33.3333%;
            transform: rotate(180deg);
            background: linear-gradient(
                90deg,
                rgba(17, 18, 23, 0) 0%,
                rgba(7, 6, 12, 1) 100%
            );
        }

        &:after {
            content: "";
            position: absolute;
            z-index: 3;
            right: 0;
            bottom: 0;
            width: 30%;
            height: 33.3333%;
            background: linear-gradient(
                90deg,
                rgba(17, 18, 23, 0) 0%,
                rgba(7, 6, 12, 1) 100%
            );
        }
    }

    &__more {
        z-index: 3;
        display: flex;
        align-items: center;
        justify-content: center;

        @media (min-width: 768px) {
            justify-content: flex-start;
            position: absolute;
            top: 55%;
            left: rem(70);
        }

        @media (max-width: 600px) {
            max-width: fit-content;
            border-radius: 7px;
            border: 1px solid $black_light;
            margin: 0 auto;
            margin-top: 40px;
            padding: 10px 20px;
        }

        &-text {
            font-size: rem(14);
            line-height: 1;
            color: $white;
        }

        &-icon {
            margin-left: rem(15);
            font-size: rem(9);
            line-height: 1;
        }
    }

    &__bottom {
        padding: rem(23) 0 rem(35);
        font-size: rem(21);
        line-height: 1.2;
        text-align: center;
        color: $grey;
        background: linear-gradient(341.52deg, #010102 18.37%, rgba(11, 14, 19, 0) 137.72%);

        @media (max-width: 600px) {
            padding: 20px 0 0;
        }

        &-text {
            font-size: rem(15);
            line-height: rem(18);
        }
    }
}

.preloaded {
    z-index: 100000000;
}

.loaded {
    z-index: 1;

    .main-top__notebook-video {
        position: absolute;
        z-index: 1;
        top: 7%;
        left: 48%;
        width: 33.3333%;
        min-width: 0;
        min-height: 0;
        transform: translateY(-7%) translateX(-48%);
        opacity: 0.8;

        @media (max-width: 600px) {
            min-width: 905px;
            margin: rem(50) auto 0;
            margin-left: 50%;
            transform: translateX(-49%);
        }
    }
}
  
@keyframes fadeIn {
    0% {
        opacity: 0;
        filter: blur(10px);
    }
    100% {
        opacity: 1;
        filter: blur(0);
    }
}
