.news-detail {
    padding: rem(59) 0 rem(80);
    background-image: url(/img/news/back.jpg);
    background-repeat: no-repeat;
    background-position: center;
    &__container {
        position: relative;
        display: flex;
        justify-content: flex-start;
        &::before {
            position: absolute;
            top: rem(-1);
            left: 0;
            width: 50%;
            height: rem(2);
            background: $black_light;
            content: "";
        }
        &::after {
            position: absolute;
            top: rem(-1);
            left: 0;
            width: 19%;
            height: rem(2);
            background: #f4f4f2;
            transition: all 0.4s ease;
            content: "";
        }
        &:hover {
            &::after {
                width: 50%;
                background: $red;
            }
        }
    }
    &__date {
        margin-right: 123px;
        @media (max-width: 1024px) {
            margin-right: rem(86);
        }
        @media (max-width: 600px) {
            display: none;
        }
        &--day {
            font-size: rem(55);
            font-weight: 700;
            line-height: 1.2;

            color: $grey_light_more;
        }
        &--month {
            font-size: rem(19);
            font-weight: 300;
            line-height: 1.2;

            color: $grey_white;
        }
    }
    &__content {
        margin-top: rem(28);
        &--img {
            margin-bottom: rem(60);
            @media (max-width: 1024px) {
                margin-bottom: rem(50);
            }
            @media (max-width: 768px) {
                margin-bottom: rem(35);
            }
            @media (max-width: 600px) {
                display: flex;
            }
        }
        &--title {
            margin-bottom: rem(20);
            font-size: rem(25);
            font-weight: 700;
            line-height: 1.2;
            @media (max-width: 1024px) {
                margin-bottom: rem(37);
                font-size: rem(23);
            }
            @media (max-width: 768px) {
                margin-bottom: rem(20);
                font-size: rem(19);
            }
            @media (max-width: 600px) {
                font-size: rem(17);
            }
        }
        &--text {
            margin-bottom: rem(160);
            @media (max-width: 1024px) {
                margin-bottom: rem(120);
            }
            @media (max-width: 768px) {
                margin-bottom: rem(100);
            }
            @media (max-width: 600px) {
                margin-bottom: rem(64);
            }
        }
    }
    &__text {
        &--paragraph {
            margin-bottom: rem(20);
            font-size: rem(19);
            font-weight: 300;
            line-height: 140%;
            color: $grey_light_more;
            @media (max-width: 1024px) {
                margin-bottom: rem(15);
                font-size: rem(17);
            }
            @media (max-width: 768px) {
                margin-bottom: rem(10);
                font-size: rem(15);
            }
            @media (max-width: 600px) {
                font-size: rem(13);
            }
        }
    }
    &__more {
        display: flex;
        justify-content: space-between;

        &--btn {
            display: flex;
            align-items: center;
            color: $grey_white;
            gap: rem(50);
        }
        &--all {
            svg {
                margin-left: rem(20);
                transform: scale(-1, 1);
            }

            &:hover {
                color: $red;
            }
        }
    }
    &__btn {
        &--prev {
            display: flex;
            align-items: center;
            justify-content: center;

            svg {
                width: rem(40);
                height: rem(40);
            }
        }
        &--next {
            display: flex;
            align-items: center;
            justify-content: center;
            transform: rotate(180deg);

            svg {
                width: rem(40);
                height: rem(40);
            }
        }
    }
    &__topic {
        display: none;
        margin-right: rem(33);
        @media (max-width: 600px) {
            display: block;
        }
        &--day {
            font-size: rem(50);
            font-weight: 700;
            line-height: 1.2;

            color: $grey_light_more;
        }
        &--month {
            font-size: rem(17);
            font-weight: 300;
            line-height: 1.2;

            color: $grey_white;
        }
    }
}
