.breadcrumbs {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.breadcrumb {
    display: flex;
    align-items: center;

    &:before {
        content: "/";
        display: block;
        font-size: rem(15);
        font-weight: 500;
        margin: 0 rem(10);
    }

    &:first-child {
        &:before {
            display: none;
        }
    }

    &-icon {
        font-size: rem(15);
        line-height: 1;
        margin-right: rem(10);
    }

    &-text {
        font-size: rem(15);
        line-height: 1;
        color: $grey_light;
    }
}
