.footer {
    padding-bottom: rem(150);
    background: linear-gradient(357.79deg, #000000, rgba(40, 46, 66, 0) 69.85%);

    .form {
        order: 2;
        width: 100%;
        margin-bottom: rem(64);

        @media screen and (max-width: 1299px) {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            margin-bottom: 0;
        }

        @media screen and (max-width: 699px) {
            flex-direction: column;
        }

        .btn {
            display: flex;
            align-items: center;
            justify-content: center;
            min-height: rem(44);
            min-width: rem(167);
        }

        &__inputs {
            display: flex;
            align-items: center;
            margin-bottom: rem(23); 

            @media screen and (max-width: 1299px) {
                width: 100%;
                margin-bottom: 0;
                margin-right: 30px;
            }

            @media screen and (max-width: 699px) {
                margin-right: 0;
                margin-bottom: 8px;
            }

            @media screen and (max-width: 499px) {
                flex-direction: column;
            }
        }

        &__input {
            width: 100%;
            margin-right: rem(20);

            @media screen and (max-width: 499px) {
                margin-right: 0;
            }

            input {
                width: 100%;
                min-height: rem(44);
                padding-left: rem(23);
                color: $grey_light_more_2;
                background: none;
                border: rem(1) solid $grey;
                border-radius: rem(10);

                @media screen and (max-width: 499px) {
                    margin-bottom: 8px;
                }

                &::placeholder {
                    color: $grey_light_more_2;
                }
            }
        }

        &__submit {
            width: auto;

            @media screen and (max-width: 499px) {
                display: flex;
                width: 100%;
                max-width: 100%;

                .btn {
                    width: 100%;
                    max-width: 100%;
                }
            }
        }
    }

    &__checkbox {
        font-size: rem(15);
        font-weight: 500;
        line-height: 120%;
        color: $grey_light_more;

        @media screen and (max-width: 1299px) {
            max-width: 216px;
            font-size: 10px;
        }

        @media screen and (max-width: 699px) {
            max-width: 100%;
            text-align: center;
        }
    }

    &__top {
        padding-bottom: rem(15);
        border-bottom: rem(1) solid $grey_light;
    }

    &__content {
        display: flex;
        margin-bottom: rem(122);
        padding-top: rem(50);

        @media screen and (max-width: 1299px) {
            flex-direction: column;
            margin-bottom: 0;
        }

        @media screen and (max-width: 600px) {
            padding-top: 0;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        max-width: rem(524);
        margin-right: rem(100);

        @media screen and (max-width: 1499px) {
            margin-right: rem(80);
        }

        @media screen and (max-width: 1299px) {
            display: flex;
            align-items: flex-start;
            max-width: 100%;
            margin-right: 0;
            margin-bottom: 40px;
        }

        @media screen and (max-width: 600px) {
            order: 1;
            flex-direction: column;
            margin-top: 30px;
        }

        &-top {
            order: 0;
            width: 100%;
        }

        &-mobile {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            width: 100%;

            @media screen and (max-width: 600px) {
                flex-direction: column;
                justify-content: center;

                &--right {
                    margin: 0 auto;
                }
            }
        }
    }

    &__logo {
        margin-bottom: rem(40);
        font-size: rem(30);
        line-height: 1;

        @media screen and (max-width: 1299px) {
            font-size: 26px;
            margin-bottom: 37px;
        }

        @media screen and (max-width: 600px) {
            display: none;
        }
    }

    &__text {
        order: 1;
        margin-bottom: rem(33);
        font-size: rem(15);
        font-weight: 600;
        line-height: 120%;
        color: $grey_light_more;

        @media screen and (max-width: 1299px) {
            font-size: 12px;
            margin-top: 48px;   
            margin-bottom: 16px;
        }
    }

    &__address {
        margin-bottom: rem(46);
        font-size: rem(20);
        font-weight: 900;
        line-height: 120%;

        @media screen and (max-width: 1299px) {
            font-size: 15px;
            margin-right: 100px;
            margin-bottom: 0;
        }

        @media screen and (max-width: 600px) {
            font-size: 19px;
            text-align: center;
            margin-right: 0;
            margin-bottom: 42px;
        }

        &-container {
            order: 3;
        }
    }

    &__contact {
        order: 9;
        display: flex;
        align-items: center;

        @media screen and (max-width: 1299px) {
            align-items: flex-end;
            flex-direction: column-reverse;
            margin-left: auto;
        }

        @media screen and (max-width: 600px) {
            align-items: center;
            flex-direction: column;
            width: 100%;
            margin-left: 0;
        }

        .btn {
            max-width: 100%;
            padding: rem(15) rem(33);
            color: $grey_light_more_2;
            white-space: nowrap;

            @media screen and (max-width: 1299px) {
                font-size: 12px;
                margin-bottom: 8px;
            }

            @media screen and (max-width: 600px) {
                margin-bottom: 0;
            }
        }
    }

    &__email {
        margin-right: rem(16);
        font-size: rem(40);
        font-weight: 900;
        line-height: 1;
        color: $grey;
        white-space: nowrap;

        @media screen and (max-width: 1299px) {
            font-size: 30px;
        }

        @media screen and (max-width: 600px) {
            font-size: 40px;
            margin-bottom: 27px;
        }
    }

    &__nav {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        width: 100%;
        margin: 0 rem(-50);
        flex-wrap: wrap;

        @media screen and (max-width: 1499px) {
            margin: 0 rem(-15);
        }

        @media screen and (max-width: 1299px) {
            margin: 0 rem(-15) rem(-28);
        }

        @media screen and (max-width: 600px) {
            order: 0;
            margin: 0;
        }

        &-column {
            width: calc(33.3333% - 100px);
            margin: 0 rem(50);

            @media screen and (max-width: 1499px) {
                width: calc(33.3333% - 30px);
                margin: 0 rem(15) rem(28);
            }

            @media screen and (max-width: 1299px) {
                margin: 0 rem(15) rem(28);
            }

            @media screen and (max-width: 600px) {
                width: 100%;
                margin: 0;
                border-bottom: 1px solid #656669;
                padding: 17px 0;
            }

            &.open {
                .footer {
                    &__list {
                        padding: 17px 0;
                    }
                }
            }

            &--nobd {
                @media screen and (max-width: 600px) {
                    border-bottom: none;
                    padding: 0;
                }

                .footer {
                    &__nav {
                        &-top {
                            padding: 0;
                        }
                    }
                }
            }

            &.open {
                .footer {
                    &__nav {
                        &-list {
                            display: block;
                            padding-top: 17px;
                        }

                        &-arrow {
                            transform: rotate(-90deg);

                            svg {
                                fill: $red;
                            }
                        }
                    }
                }
            }
        }

        &-title {
            display: block;
            font-size: rem(20);
            font-weight: 600;
            line-height: 120%;
            color: $grey_white;

            @media screen and (max-width: 1299px) {
                font-size: 12px;
                margin-bottom: 25px;
            }

            @media screen and (max-width: 600px) {
                margin-bottom: 0;
            }

            &--item {
                margin-bottom: 67px;
                @media screen and (max-width: 600px) {
                    width: 100%;
                    border-bottom: 1px solid #656669;
                    padding: 17px 30px !important;
                    margin-bottom: 0;
                }

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        &-list {
            margin-top: rem(30);

            @media screen and (max-width: 600px) {
                @include tr('all');
                display: none;
                margin-bottom: 0;
                margin-top: 0;
                animation: show .4s ease-in-out;
            }
        }

        &-item {
            display: block;
            margin-bottom: rem(12);
            font-size: rem(15);
            line-height: 120%;
            color: $grey_light;

            @media screen and (max-width: 1299px) {
                font-size: 14px;
            }

            @media screen and (max-width: 600px) {
                padding: 0 30px;
                margin-bottom: 17px;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }

        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;

            @media screen and (max-width: 600px) {
                padding: 0 30px;
            }
        }

        &-arrow {
            display: none;
            align-items: center;
            justify-content: center;
            font-size: 12px;
            line-height: 1;
            transition: all .4s ease-in-out;
            transform: rotate(90deg);

            @media screen and (max-width: 600px) {
                display: flex;
            }
        }
    }

    &__bottom {
        display: flex;
        color: $grey;

        @media screen and (max-width: 1299px) {
            flex-wrap: wrap;
            margin-top: 60px;
        }

        &-items {
            display: flex;
            width: auto;
            margin: 0 auto;

            @media screen and (max-width: 600px) {
                flex-wrap: wrap;
            }
        }
        
        &-item {
            display: flex;
            align-items: center;
            font-size: rem(15);
            line-height: 120%;

            @media screen and (max-width: 1299px) {
                font-size: 12px;
            }

            &:after {
                display: block;
                width: rem(1);
                height: rem(22);
                margin: 0 rem(15);
                background: $grey;
                content: "";
            }

            &:nth-child(4) {
                &:after {
                    display: none;
                }
            }
        }
    }

    &__copyrights {
        margin-left: auto;
        font-size: rem(15);
        line-height: 120%;

        @media screen and (max-width: 1299px) {
            width: 100%;
            text-align: center;
            margin-top: 10px;
        }

        @media screen and (max-width: 600px) {
            text-align: left;
        }
    }
}
