.s-home-products {
    position: relative;
    margin-top: rem(-40);
    padding-bottom: rem(67);

    @media screen and (max-width: 600px) {
        margin-top: 0;
    }
    
    .product {
        display: flex;
        flex-direction: column-reverse;
        justify-content: flex-end;

        @media (min-width: 1600px) {
            flex-direction: column;
        }

        &__link {
            display: flex;
            align-items: center;
            justify-content: center;
            width: rem(40);
            height: rem(40);
            border: rem(2) solid $grey_light;
            margin-bottom: rem(37);
            border-radius: 100%;
        }

        &__top {
            display: flex;
            justify-content: flex-end;
            position: relative;
            margin-top: auto;

            @media screen and (max-width: 600px) {
                margin-top: 14px;
            }

            &-text {
                transform: translateX(rem(-30));
                font-size: rem(120);
                font-weight: 900;
                line-height: 1;
                text-transform: uppercase;
                color: #4b4b4e;

                
            }

            &-caption {
                display: flex;
                position: absolute;
                z-index: 1;
                top: 50%;
                left: 50%;
                width: 100%;
                overflow: hidden;
                transform: translate(-50%, -50%);

                @media screen and (max-width: 600px) {
                    top: 0;
                    transform: translate(-50%, 0);
                }
            }
        }

        &__info {
            padding: rem(45) rem(35) rem(24);

            @media (min-width: 992px) {
                padding: rem(54) rem(45) rem(24);
            }

            &-item, &-title {
                display: none;
                // animation: show 1.5s ease-in-out;
            }

            &-description {
                font-size: rem(10);
                line-height: 1.2;
                color: $grey_light_more;
            }
        }

        &__made {
            display: none;
        }

        &__img {
            z-index: 2;
            width: auto;
            height: rem(180);
            transition: all 1s ease-in-out;

            @media (min-width: 992px) {
                height: rem(230);
                min-height: rem(230);
                margin-right: rem(-30);
            }

            @media screen and (max-width: 600px) {
                height: rem(251);
            }

            img {
                display: block;
                width: auto;
                height: 100%;
            }
        }

        &__name {
            margin-bottom: rem(14);
            font-size: rem(40);
            font-weight: 900;
            line-height: 1;
            color: $grey_light_more_2;
        }

        &__subtitle {
            display: block;
            margin-bottom: rem(9);
            font-size: rem(10);
            line-height: 1.2;
            color: $grey_white;
        }

        &.swiper-slide-active {
            @media screen and (min-width: 1299px) {
                flex-direction: column;
    
                &:after {
                    // background-image: url('.././img/svg/noize-large.svg');
                }
        
                .product {
                    &__top {
                        position: relative;
                        display: flex;
                        justify-content: flex-end;

                        &-text {
                            font-size: 260px;
                            line-height: 1;
                            font-weight: 900;
                            text-transform: uppercase;
                            color: #4b4b4e;
                            transform: translateX(0);
                        }

                        &-caption {
                            position: absolute;
                            z-index: 1;
                            top: 50%;
                            left: 0;
                            transform: translateY(-50%);
                            display: flex;
                            width: 100%;
                            overflow: hidden;
                        }
                    }
                    
                    &__img {
                        min-height: 357px;
                        margin-top: -50px;
                        margin-left: auto;
                        margin-right: 80px;
                    }

                    &__info {
                        padding-left: 55px;
                        padding-right: 70px;
                        padding-bottom: 43px;

                        &-column {
                            margin-right: 30px;

                            &:last-child {
                                display: flex;
                                justify-content: flex-end;
                                align-items: flex-end;
                                margin-left: auto;
                            }
                        }

                        &-columns {
                            display: flex;
                        }

                        &-title {
                            font-family: $secondary_font;
                            font-size: 10px;
                            line-height: 1;
                            font-weight: 900;
                            color: $grey_light_more;
                            margin-bottom: 13px;
                        }

                        &-caption, &-text {
                            font-family: $secondary_font;
                            font-size: 10px;
                            line-height: 120%;
                            color: $grey_light_more;
                        }

                        &-text {
                            font-weight: 900;
                        }

                        &-item {
                            display: block;
                            margin-bottom: 5px;
                        }

                        &-description {
                            max-width: 194px;
                        }
                    }

                    &__name {
                        display: flex;
                        align-items: center;
                        margin-bottom: 14px;

                        &:after {
                            content: '';
                            display: block;
                            height: 1px;
                            width: 50px;
                            background: #050303;
                            margin-left: 27px;
                        }
                    }

                    &__made {
                        display: block;
                        font-size: 14px;
                        font-weight: 600;
                        color: $grey_white;
                    }

                    &__link {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }

    .swiper-slide {
        position: relative;
        height: auto;
        transition: opacity .8s ease-in-out;
        background-image: url("../img/global/noise.png");
        opacity: 1;

        @media screen and (max-width: 1299px) and (min-width: 600px) {
            width: 375px !important;
        }

        @media screen and (min-width: 1299px) {
            opacity: 0;
        }

        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            background: #3f3f42;
            // background-image: url('.././img/svg/noize.svg');
            background-size: cover;
            opacity: .9;
        }

        &-active {
            @media screen and (min-width: 1299px) {
                opacity: 1;
                width: 75% !important;
            }
        }

        &-next {
            @media screen and (min-width: 1299px) {
                opacity: 1;
                width: calc(25% - 30px) !important;
                margin-right: 70px !important;
            }
        }
    }

    .swiper-pagination {
        top: rem(-50);
        text-align: left;

        @media screen and (max-width: 600px) {
            display: none;
        }

        &-bullet {
            width: rem(15);
            height: rem(15);
            margin-right: rem(26) !important;
            background: #d9d9d9;
            opacity: 0.4;

            &-active {
                opacity: 1;
            }
        }
    }

    &__slider {
        height: rem(541);
        overflow: visible;
        animation: show 0.4s ease;
        margin-top: rem(40);

        @media screen and (max-width: 600px) {
            height: auto;
            margin-top: 4px;
        }

        &-navigation {
            display: none;

            @media screen and (max-width: 600px) {
                display: flex;
                align-items: center;
                justify-content: center;
                margin-top: 20px;
                margin-bottom: 27px;
            }

            .slider-arrow-prev, .slider-arrow-next {
                width: 40px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                border: 1px solid $grey_light;
                border-radius: 100%;
                margin-right: 20px;

                &:last-child {
                    margin-right: 0;
                }

                &:active {
                    border: 1px solid $white;
                }
            }

            .slider-arrow-prev {
                transform: rotate(180deg);
            }
        }
    }

    &__sliders {
        &-container {
            position: relative;
            width: 100%;
            min-height: rem(540);
            margin-bottom: rem(50);

            @media (max-width: 600px) {
                min-height: 0;
                margin-bottom: 0;
            }

            &:after {
                content: "";
                position: absolute;
                top: 80%;
                left: rem(-70);
                width: calc(100% + 140px);
                height: rem(54);
                transform: translateY(-80%);
                background: $red;

                @media (max-width: 600px) {
                    display: none;
                }
            }
        }
    }

    &__categories {
        padding-top: rem(35);
        border-top: rem(2) solid #4d4d4d;

        &-wrapper {
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            align-items: center;
            margin: 0 rem(-1);

            @media (min-width: 768px) {
                flex-direction: row;
            }
        }
    }

    &__category {
        width: 100%;

        @media (min-width: 768px) {
            width: calc(33.33% - 2px);
        }

        @media (min-width: 992px) {
            width: calc(25% - 2px);
        }

        &-slider {
            position: relative;
            @media screen and (max-width: 992px) {
                &:after {
                    content: "";
                    display: none;
                    position: absolute;
                    top: 60%;
                    left: rem(-70);
                    width: calc(100% + 140px);
                    height: rem(54);
                    transform: translateY(-60%);
                    background: $red;
                }
            }
        }

        &.active {
            .s-home-products {
                &__category {
                    &-text {
                        color: $grey_dark;
                        background: $grey_light_more;
        
                        &:after {
                            content: "";
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: rem(4);
                            height: 100%;
                            background: $red;
                        }
                    }

                    &-slider {
                        &:after {
                            @media screen and (max-width: 600px) {
                                display: block;
                            }
                        }
                    }
                }
            }
        }

        &.is-disabled {
            .s-home-products {
                &__category {
                    &-text {
                        color: $grey_dark;
                        pointer-events: none;
                    }
                }
            }
        }
        
        &-text {
            @include tr("all");
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            width: 100%;
            margin: 0 rem(1) rem(2);
            padding: rem(13) rem(4);
            cursor: pointer;
            font-size: rem(14);
            text-align: center;
            color: $grey_light;
            background: $black_light;

            @media (min-width: 768px) {
                font-size: rem(15);
            }

            &:hover {
                color: $grey_dark;
                background: $grey_light_more;
            }
        }
    }
}
