.animation-wrapper {
    @media (min-width: 1200px) {
        width: 50%;
    }
}

.sphere-animation {
    position: absolute;
    z-index: -1;
    top: -52px;
    left: 0;
    width: rem(600);
    height: rem(600);

    @media (min-width: 768px) {
        top: rem(115);
        width: rem(800);
        height: rem(800);
    }

    @media (min-width: 992px) {
        top: rem(75);
        left: 30%;
        width: rem(1000);
        height: rem(1000);
        transform: translateX(-30%);
    }

    @media (min-width: 1200px) {
        top: rem(115);
        left: 20%;
        width: rem(580);
        height: rem(580);
        transform: translateX(-20%);
    }
}

.sphere path {
    fill: url(#sphereGradient);
    stroke-width: rem(1);
    stroke: rgba(80, 80, 80, 0.35);
    backface-visibility: hidden;
}

@media (min-width: 500px) {
    .sphere path {
        stroke-width: 0.4px;
    }
}
