.news-item {
    .preview {
        &__date {
            text-align: center;
            &--day {
                font-weight: 700;
                font-size: rem(55);
                line-height: 0.8;

                color: $grey_white;
                @media (max-width: 600px) {
                    font-size: rem(45);
                    line-height: 120%;
                }
            }
            &--month {
                margin: 0;

                padding: 0;
                font-weight: 300;
                font-size: rem(19);
                line-height: 1.2;

                color: $grey_white;
                @media (max-width: 600px) {
                    font-size: rem(15);
                }
            }
        }
        &__img {
            width: 223.12px;
            height: rem(150);

            margin-left: rem(80);
            @media (max-width: 1024px) {
                margin-left: rem(55);
            }
            @media (max-width: 991px) {
                display: none;
            }
            picture {
                height: 100%;
                img {
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
        &__more {
            display: none;
            @media (max-width: 600px) {
                display: flex;
                margin-top: rem(17);
            }
            &--btn {
                font-weight: 300;
                font-size: rem(12);
                line-height: 1.2;

                color: $grey_light;
            }
            &--arrow {
                margin-left: rem(23);
            }
        }
    }
    .text {
        &__title {
            font-weight: 700;
            font-size: rem(21);
            line-height: 1.2;

            color: $grey_white;
            margin-bottom: rem(10);
            @media (max-width: 1024px) {
                font-size: rem(19);
                margin-bottom: rem(15);
            }
            @media (max-width: 991px) {
                font-size: rem(17);
                margin-bottom: rem(14);
            }
            @media (max-width: 600px) {
                font-size: rem(15);
            }
        }
        &__prefix {
            font-weight: 300;
            font-size: rem(15);
            color: $grey_light_more;

            line-height: 1.2;

            margin-bottom: rem(34);
            @media (max-width: 1024px) {
                font-size: rem(14);
                margin-bottom: rem(43);
            }
            @media (max-width: 991px) {
                margin-bottom: rem(30);
            }
            @media (max-width: 600px) {
                font-size: rem(12);
                margin: 0;
            }
        }
        &__more {
            display: flex;
            @media (max-width: 600px) {
                display: none;
            }
            &--btn {
                font-weight: 300;
                font-size: rem(15);
                line-height: 1.2;

                color: $grey_light;
                @media (max-width: 1024px) {
                    font-size: rem(14);
                }
                @media (max-width: 991px) {
                }
            }
            &--arrow {
                margin-left: rem(40);
            }
        }
    }
    &__content {
        display: flex;
        align-items: center;

        margin-bottom: rem(15);
        @media (max-width: 991px) {
            position: relative;
            margin-bottom: rem(20);
            padding: rem(17) 0 0;
            &:hover {
                &::after {
                    width: 80%;
                    background: $red;
                }
            }
            &::before {
                content: "";
                position: absolute;
                top: rem(-1);
                left: 0;
                width: 80%;
                height: rem(2);
                background: $black_light;
            }
            &::after {
                content: "";
                position: absolute;
                top: rem(-1);
                left: 0;
                width: 35%;
                height: rem(2);
                transition: all 0.4s ease;
                background: #f4f4f2;
            }
        }
        @media (max-width: 600px) {
            flex-direction: column;
            padding: 0;
            margin-bottom: rem(32);
            &:hover {
                &::after {
                    width: 100%;
                    background: $red;
                }
            }
            &::before {
                content: "";
                position: absolute;
                top: rem(-1);
                left: 0;
                width: 100%;
                height: rem(2);
                background: $black_light;
            }
            &::after {
                content: "";
                position: absolute;
                top: rem(-1);
                left: 0;
                width: 30%;
                height: rem(2);
                transition: all 0.4s ease;
                background: #f4f4f2;
            }
        }
        &:hover {
            .news-item__content--preview {
                &::after {
                    width: 100%;
                    background: $red;
                }
            }
        }
        &--preview {
            display: flex;
            align-items: flex-start;
            position: relative;

            padding-top: rem(28);
            padding-right: rem(71);
            @media (max-width: 991px) {
                padding-top: 0;
                padding-right: rem(92);
            }
            @media (max-width: 600px) {
                justify-content: space-between;
                width: 100%;
                padding-right: 0;
                margin-bottom: rem(20);
            }
            &::before {
                content: "";
                position: absolute;
                top: rem(-1);
                left: 0;
                width: 100%;
                height: rem(2);
                background: $black_light;
                @media (max-width: 991px) {
                    display: none;
                }
            }
            &::after {
                content: "";
                position: absolute;
                top: rem(-1);
                left: 0;
                width: rem(191);
                height: rem(2);
                transition: all 0.4s ease;
                background: #f4f4f2;
                @media (max-width: 991px) {
                    display: none;
                }
            }
        }
        &--text {
            margin-left: rem(47);
            @media (max-width: 1024px) {
                margin-left: rem(29);
            }
            @media (max-width: 991px) {
                margin-left: 0;
            }
        }
    }
}
