.search {
    order: 1;
    position: relative;
    @media screen and (max-width: 991px) {
        position: static;
    }
    .form--search {
        display: flex;
        align-items: center;

        .form {
            &__input {
                position: relative;
                margin-right: rem(13);

                @media screen and (max-width: 991px) {
                    display: flex;
                    align-items: center;
                    width: 100%;
                }

                &.focused {
                    &:after {
                        content: "";
                        position: absolute;
                        z-index: 1;
                        bottom: 0;
                        left: rem(47);
                        width: 50%;
                        height: rem(1);
                        background: #d9d9d9;

                        @media screen and (max-width: 991px) {
                            display: none;
                        }
                    }

                    input {
                        border-bottom-left-radius: 0;
                        border-bottom-right-radius: 0;
                    }

                    .search {
                        &__result {
                            display: block;
                        }
                    }
                }

                input {
                    @include tr("all");
                    width: rem(559);
                    height: 100%;
                    min-height: rem(26);
                    font-size: rem(15);
                    line-height: 1;
                    color: $black;
                    background: $black_light;
                    border-radius: rem(8);
                    padding-left: rem(47);

                    @media screen and (max-width: 1439px) {
                        width: rem(242);
                        font-size: rem(14);
                    }

                    @media screen and (max-width: 991px) {
                        width: 100%;
                        color: $white;
                        background: none;
                        padding-left: 0;
                    }

                    &::placeholder {
                        color: $grey_light;
                    }
                }

                &-icon {
                    position: absolute;
                    top: 50%;
                    left: rem(16);
                    transform: translateY(-50%);
                    font-size: rem(16);
                    line-height: 1;

                    @media screen and (max-width: 991px) {
                        position: static;
                        top: unset;
                        left: unset;
                        transform: translateY(0);
                        font-size: rem(25);

                        svg {
                            fill: $black_light;
                        }
                    }
                }
            }

            &__submit {
                font-size: rem(15);
                line-height: 1;

                @media screen and (max-width: 1439px) {
                    font-size: rem(14);
                }

                @media screen and (max-width: 991px) {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    min-width: rem(32);
                    max-width: fit-content;
                    min-height: rem(32);
                    border: rem(1) solid $black_light;
                    padding: rem(2) rem(6);
                    animation: show 0.4s ease-in-out;
                    font-size: rem(18);
                    line-height: 1;
                    background: none;
                    margin-right: rem(20);
                    border-radius: rem(7);

                    .btn {
                        padding: 0;
                        background: none;
                    }

                    svg {
                        fill: $grey_light;
                    }
                }

                &-icon {
                    display: none;

                    @media screen and (max-width: 991px) {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                }

                &-text {
                    @media screen and (max-width: 991px) {
                        display: none;
                    }
                }

                .btn {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    min-height: 26px;
                }
            }
        }
    }

    &__result {
        display: none;
        position: absolute;
        z-index: 6;
        width: 100%;
        padding: rem(10);
        animation: showFromTop 0.4s ease-in-out;
        font-size: rem(15);
        line-height: 1.2;
        color: $grey_light;
        background: $black_light;
        border-bottom-left-radius: rem(8);
        border-bottom-right-radius: rem(8);

        @media screen and (max-width: 991px) {
            position: fixed;
            z-index: 102;
            top: unset;
            bottom: rem(-2);
            left: 0;
            height: calc(100vh - 64px);
            min-height: calc(100vh - 64px);
            font-size: rem(12);
            background: $black;
            border-radius: 0;
        }

        &-title {
            margin-bottom: rem(10);
        }

        &-items {
            display: flex;
            flex-direction: column;
            padding-left: rem(30);
        }

        &-item {
            margin-bottom: rem(10);
        }
    }
}
