input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

input[type="number"] {
    -moz-appearance: textfield;
}

.form {
    &__checkboxes {
        margin-bottom: rem(40);
    }

    &__checkbox {
        display: flex;
        align-items: flex-start;
        position: relative;
        margin-bottom: rem(10);

        &:last-child {
            margin-bottom: 0;
        }

        &-btn {
            position: relative;
            min-width: rem(20);
            min-height: rem(20);
            border: rem(1) solid $grey;
            border-radius: rem(5);
            margin-right: rem(20);

            &:after {
                content: "";
                position: absolute;
                z-index: 2;
                top: 0;
                right: 0;
                width: rem(20);
                height: rem(20);
                opacity: 0;
                background: url(".././img/svg/checked-red.svg");
                background-repeat: no-repeat;
                background-size: 100% 100%;
            }
        }

        &-input {
            position: absolute;
            z-index: 2;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            cursor: pointer;

            &:checked + .form__checkbox-btn {
                border: none;
            }

            &:checked + .form__checkbox-btn:after {
                opacity: 1;
            }
        }

        &-text {
            font-size: rem(15);
            line-height: 1.2;
        }
    }

    &__submit {
        display: flex;
        width: 100%;
        max-width: 100%;
    }
}
