.tabs {
    margin-bottom: rem(25);
    @media (max-width: 991px) {
        margin-bottom: rem(50);
    }
    @media (max-width: 991px) {
        margin-bottom: rem(30);
    }
    &__content {
        display: flex;
        justify-content: right;
        @media (max-width: 768px) {
            justify-content: center;
        }
        @media (max-width: 405px) {
            flex-wrap: wrap;
            margin-bottom: rem(-8);
        }
        &-item {
            display: block;
            border: rem(1) solid $white;
            border-radius: rem(5);

            font-weight: 300;
            font-size: rem(12);
            line-height: 1.2;

            padding: rem(5) rem(12);
            margin-left: rem(14);
            &:first-child {
                margin-left: 0;
            }
            @media (max-width: 768px) {
                margin-left: rem(13);
            }
            @media (max-width: 405px) {
                margin-bottom: rem(8);
            }
        }
    }
}
