.timetable {
    .calendar-events {
        width: 100%;

        &__items {
            display: flex;

            @media screen and (max-width: 1299px) {
                flex-wrap: wrap;
            }
        }

        &__photo {
            position: relative;
            width: 100%;
            min-height: rem(160);
            margin-top: rem(12);

            @media (max-width: 991px) {
                min-height: rem(103);
            }

            img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__info {
            display: flex;
            flex: 1 0 auto;
            flex-direction: column;
        }

        &__column {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
            width: 14.5%;
            min-height: 100%;
            padding: 0 rem(10);
            border-left: rem(1) solid $white;

            &:nth-child(2), &:last-child {
                width: 31.5%;

                .calendar-events {
                    &__date {
                        display: flex;
                        justify-content: space-between;

                        &-number {
                            font-size: rem(120);

                            @media (max-width: 991px) {
                                font-size: 70px;
                            }
                        }

                        &-text {
                            font-size: rem(19);
                        }
                    }

                    &__place {
                        margin-left: auto;
                    }

                    &__photo {
                        margin-top: 0;
                    }
                }
            }

            &:nth-child(2) {
                @media (max-width: 991px) {
                    border-left: none;
                }
                .calendar-events {
                    &__photo {
                        min-height: rem(237);
                        margin-top: rem(16);

                        @media (max-width: 991px) {
                            min-height: rem(152);
                        }
                    }

                    &__item {
                        border-top: rem(1) solid $grey_dark;
                        
                    }
                }
            }

            &:last-child {
                flex-wrap: wrap;
                flex-direction: row;
            }

            &:first-child {
                border-left: none;
                border-right: rem(1) solid $white;
                .calendar-events {
                    &__photo {
                        margin-top: 0;
                        margin-bottom: rem(12);
                    }

                    &__item {
                        &:first-child {
                            border-top: rem(1) solid $white;
                        }
                    }
                }
            }

            @media (max-width: 991px) {
                width: 40% !important;
                flex-wrap: wrap;

                &:nth-child(2n) {
                    width: 60% !important;
                }
            }
        }

        &__item {
            @include tr("all");
            display: flex;
            flex: 1 0 auto;
            flex-direction: column;
            width: 100%;
            padding: rem(14) 0;
            margin-right: rem(20);
            border-top: rem(1) solid #000;

            @media (max-width: 991px) {
                margin-right: 0 !important;
            }

            &:hover {
                border-top: rem(1) solid $red !important;

                .calendar-events {
                    &__date {
                        &-number {
                            color: $red;
                        }
                    }
                }
            }

            &:last-child {
                margin-right: 0;
                padding-bottom: 0;
            }
        }

        &__col {
            width: 50%;
            padding: 0 rem(10);
            border-left: rem(1) solid $white;

            @media (max-width: 991px) {
                padding: 0;
            }

            &:nth-child(2) {
                @media (max-width: 991px) {
                    padding-left: 10px;
                }
            }

            .calendar-events {
                &__date {
                    flex-direction: column !important;

                    &-number {
                        font-size: rem(55) !important;

                        @media (max-width: 991px) {
                            font-size: rem(40) !important;
                        }
                    }
                }

                &__item {
                    padding: rem(14) 0;
                }

                &__place {
                    margin-left: 0 !important;
                }
            }

            &:first-child {
                border-left: none;

                .calendar-events {
                    &__item {
                        &:last-child {
                            border-top: rem(1) solid $white;
                        }
                    }
                }
            }
        }

        &__date {
            margin-bottom: rem(52);

            @media (max-width: 991px) {
                margin-bottom: 30px;
            }

            &-number {
                @include tr("all");
                font-size: rem(55);
                font-weight: 900;
                line-height: 1;

                @media (max-width: 991px) {
                    font-size: rem(40) !important;
                }
            }

            &-month {
                font-size: rem(19);
                font-weight: 900;
                line-height: 1;

                @media (min-width: 1399px) {
                    width: 50%;
                }

                @media (max-width: 991px) {
                    font-size: rem(12) !important;
                }
            }
        }

        &__place {
            margin-top: auto;
            &-country {
                font-size: rem(15);
                font-weight: 900;
                line-height: 1.2;
            }

            &-city {
                font-size: rem(12);
                line-height: 1.2;
            }
        }
    }

    .calendar-news {
        &__items {
            display: flex;
            flex-direction: column;

            @media (min-width: 1399px) {
                flex-direction: row;
            }
        }

        &__item {
            position: relative;
            padding: rem(5) 0 rem(32);

            @media (min-width: 768px) {
                margin-right: rem(15);
                padding: rem(30) 0;
                padding-left: rem(15);
                border-left: rem(1) solid $white;
            }

            &:after {
                @include tr("all");
                content: "";
                position: absolute;
                top: rem(-1);
                left: 0;
                width: rem(191);
                height: rem(2);
                background: $white;

                @media (min-width: 768px) {
                    left: rem(15);
                }
            }

            &:before {
                content: "";
                position: absolute;
                top: rem(-1);
                left: 0;
                width: 100%;
                height: rem(2);
                background: $black_light;

                @media (min-width: 768px) {
                    left: rem(15);
                }
            }

            &:hover {
                .calendar-news {
                    &__date {
                        &-number {
                            color: $red;
                        }
                    }

                    &__more {
                        &:after {
                            content: url(".././img/svg/arrow-menu-active.svg");
                        }
                    }
                }
                &::after {
                    width: 100%;
                    background: $red;
                }
            }

            &:first-child {
                padding-left: 0;
                border-left: none;

                &:after, &:before {
                    left: 0;
                }
            }

            &-top {
                display: flex;
                align-items: center;
                margin-bottom: rem(20);

                @media (min-width: 768px) {
                    align-items: flex-start;
                    margin-bottom: 0;
                }
            }
        }

        &__date {
            text-align: center;

            @media (min-width: 768px) {
                margin-right: rem(72);
            }

            &-number {
                @include tr("all");
                font-size: rem(40);
                font-weight: 700;
                line-height: 1.2;

                @media (min-width: 768px) {
                    font-size: rem(55);
                    font-weight: 900;
                    line-height: 1;
                }
            }

            &-text {
                font-size: rem(19);
                font-weight: 900;
                line-height: 1;
            }
        }

        &__more {
            display: flex;
            align-items: center;
            justify-content: flex-end;

            @media (min-width: 768px) {
                margin-bottom: rem(25);
            }

            &:after {
                content: url(".././img/svg/arrow-half-dark.svg");
                display: flex;
            }

            &-text {
                margin-right: rem(40);
                font-size: rem(16);
                line-height: 1;
                color: $grey_light;
            }
        }

        &__img {
            display: none;
            position: relative;
            min-height: rem(237);
            margin-bottom: rem(74);

            @media (min-width: 768px) {
                display: block;
            }

            img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__info {
            width: 100%;
        }

        &__title {
            margin-bottom: rem(10);
            font-size: rem(15);
            font-weight: 700;
            line-height: 1;

            @media (min-width: 768px) {
                margin-bottom: rem(25);
                font-size: rem(21);
                font-weight: 900;
                line-height: 1.2;
            }
        }

        &__text {
            font-size: rem(13);
            font-weight: 300;
            line-height: 1.4;

            @media (min-width: 768px) {
                font-size: rem(15);
                font-weight: 400;
                line-height: 1.2;
            }
        }
    }

    &__top {
        .l-default {
            padding-right: 0;
        }

        &-container {
            display: flex;
            align-items: flex-end;
            justify-content: flex-start;
            flex-wrap: wrap;
            margin-bottom: 72px;

            @media (min-width: 1399px) {
                flex-direction: row;
                min-height: rem(314);
                margin-bottom: 0;
            }
        }

        &-content {
            display: flex;
            align-items: center;
            align-self: center;

            @media (max-width: 1399px) {
                width: 50%;
            }
        }
    }

    &__tabs {
        display: flex;
        align-items: flex-start;
        width: 100%;
        min-width: rem(160);

        @media (min-width: 1399px) {
            width: rem(160);
            margin-right: rem(179);
        }

        @media (max-width: 1399px) {
            order: 9;
        }
        
        &-items {
            display: flex;
            width: 100%;

            @media (min-width: 1399px) {
                display: block;
                min-height: rem(160);
                transform: rotate(-90deg) translate(-100%);
                transform-origin: top left;
                border-left: rem(2) solid $red;
                padding-left: rem(30);
            }
        }

        &-active {
            display: none;

            @media (min-width: 1399px) {
                display: flex;
                width: rem(160);
                min-width: rem(160);
                margin-right: rem(179);
            }
        }

        &-result {
            display: flex;
            align-items: center;
            width: 100%;
            min-height: rem(160);
            font-size: rem(70);
            line-height: 1;
            transform: rotate(-90deg) translate(-100%);
            transform-origin: top left;
        }
    }

    &__tab {
        @include tr("all");
        cursor: pointer;
        position: relative;
        margin-bottom: rem(37);
        font-size: rem(30);
        line-height: 1.2;
        color: $grey;

        @media (max-width: 1399px) {
            display: flex;
            flex-direction: row-reverse;
            align-items: center;
            font-size: 19px;
            margin-right: 65px;
            margin-bottom: 0;
        }

        &:last-child {
            margin-bottom: 0;
            margin-right: 0;
        }

        &.active {
            color: $grey_light_more;

            &:after {
                content: "";
                position: absolute;
                top: 50%;
                left: rem(-65);
                width: rem(19);
                height: rem(3);
                transform: translateY(-50%);
                transform: rotate(180deg);
                background: $red;

                @media (max-width: 1399px) {
                    position: static;
                    left: unset;
                    display: block;
                    margin-right: 8px;
                }
            }
        }
    }

    &__month {
        position: static;
        margin-left: auto;
        font-size: rem(120);
        font-weight: 700;
        line-height: 1.2;
        color: $grey_light_more;
        transform: translateX(30px);

        @media (min-width: 1399px) {
            position: absolute;
            right: 0;
            font-size: rem(240);
            font-weight: 900;
            line-height: 1;
            transform: translateX(40px);
        }
    }

    &__link {
        order: 0;
        display: flex;
        align-items: center;
        margin-right: rem(68);

        @media (max-width: 1399px) {
            margin-right: 0;
            margin-top: 40px;
        }

        &-text {
            display: flex;
            align-items: center;
            margin-right: rem(90);
            font-size: rem(15);
            font-weight: 500;
            line-height: 1.2;
            color: $grey_light_more;
            gap: rem(40);

            @media (max-width: 1399px) {
                margin-left: auto;
                margin-right: 0;
            }
        }

        &-icon {
            display: flex;
            align-items: center;
            justify-content: center;
            width: rem(40);
            height: rem(40);
            border: rem(1) solid $grey_light_more;
            border-radius: 100%;
            
        }
    }

    &__title {
        order: 1;
        font-size: rem(19);
        font-weight: 500;
        line-height: 1.2;
        padding-left: 0px;

        @media (max-width: 1399px) {
            width: auto;
        }
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        align-items: flex-end;

        @media (min-width: 1399px) {
            flex-wrap: nowrap;
            flex-direction: row;
            min-height: rem(547);
        }
    }

    &__item {
        align-self: flex-start;
        animation: show 0.4s ease-in-out;
    }
}
