.s-home-articles {
    padding: rem(80) 0 rem(40);

    @media (min-width: 768px) {
        padding: rem(155) 0 rem(40);
    }

    .l-default {
        @media (min-width: 1200px) {
            max-width: rem(1220);
        }
        @media (min-width: 1600px) {
            padding: 0;
        }
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        @media (min-width: 1200px) {
            flex-wrap: nowrap;
            margin: 0 -100px;
        }
    }

    &__item {
        @media (min-width: 768px) {
            width: calc(50% - 15px);
        }

        @media (min-width: 1200px) {
            width: calc(50% - 200px);
            margin: 0 rem(100);
        }

        &:last-child {
            margin-top: rem(60);

            @media (min-width: 768px) {
                margin-top: rem(200);
            }
        }

        &-img {
            position: relative;
            min-height: rem(213);
            margin-bottom: rem(25);

            @media (min-width: 768px) {
                min-height: rem(400);
                margin-bottom: rem(40);
            }

            img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &-title {
            margin-bottom: rem(12);
            font-size: rem(25);
            font-weight: 600;
            line-height: rem(30);

            @media (min-width: 1200px) {
                margin-bottom: rem(15);
                font-size: rem(40);
                font-weight: 900;
                line-height: 1.2;
            }
        }

        &-subtitle {
            margin-bottom: rem(25);
            font-size: rem(19);
            line-height: 1.2;
            color: $grey_light;
        }

        &-text, &-list {
            font-weight: 500;
            color: $grey_light_more;
        }

        &-text {
            margin-left: rem(25);
            font-size: rem(13);
            line-height: 160%;

            @media (min-width: 768px) {
                margin-left: 0;
                font-size: rem(14);
            }

            p {
                margin-top: 0;
                margin-bottom: rem(20);
            }
        }

        &-list {
            margin-bottom: rem(40);
            margin-left: rem(25);
            padding-left: rem(30);
            font-size: rem(13);
            line-height: rem(20);

            @media (min-width: 768px) {
                font-size: rem(14);
                line-height: 1.2;
            }

            @media (min-width: 768px) {
                margin-left: 0;
            }

            li {
                margin-bottom: rem(10);

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        &-bottom {
            padding-top: rem(10);
            border-top: rem(1) solid $red;
        }
    }

    &__link {
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: $grey_dark;

        &-text {
            font-size: rem(16);
            line-height: 1;
        }

        path {
            @include tr("all");
        }

        &:hover {
            color: $red;

            path {
                stroke: $red;
            }
        }
    }
}
