.popup {
    display: none;

    &-form {
        max-width: rem(430);
        padding: rem(57) rem(65) rem(65);
        background: linear-gradient(
            36deg,
            rgba(66, 67, 70, 1) 0%,
            rgba(29, 30, 33, 1) 100%
        );
        border-radius: rem(20);
        box-shadow: 0 rem(4) rem(4) rgba(0, 0, 0, 0.25);

        @media (max-width: 992px) {
            padding: 40px 20px;
        }

        .form {
            .focused {
                label {
                    color: $white;
                }
            }

            &__title {
                position: relative;
                max-width: fit-content;
                margin-bottom: rem(30);
                padding-bottom: rem(3);
                font-size: rem(21);
                font-weight: 500;
                line-height: 1.2;

                &::after {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: rem(1);
                    background: $red;
                    content: "";
                }
            }

            &__inputs {
                display: flex;
                align-items: center;
                flex-wrap: wrap;
            }

            &__input {
                display: flex;
                width: 100%;
                margin-bottom: rem(20);
                flex-direction: column;

                label {
                    margin-bottom: rem(5);
                    font-size: rem(15);
                    line-height: 1;
                    color: $grey_light;
                    @include tr("all");

                    &.error {
                        font-size: rem(8);
                        line-height: 1.2;
                        color: $red !important;
                    }
                }

                input {
                    min-height: rem(30);
                    padding: 0 rem(12);
                    font-size: rem(14);
                    color: $white;
                    background: none;
                    border: rem(1) solid $grey;
                    border-radius: rem(5);
                    @include tr("all");

                    &:focus {
                        color: $white;
                        border: rem(1) solid $white;
                    }

                    &::placeholder {
                        font-size: rem(12);
                        color: $grey_light;
                    }
                }
            }

            &__submit {
                padding: rem(10) rem(12);
                font-size: rem(15);
                line-height: 1;
            }
        }
    }
    &-categories{
        width: rem(1024);
        overflow: hidden;
        background: #424346;
        
        &__container{
            background: linear-gradient(357.79deg, #202020 1.85%, rgba(40, 46, 66, 0) 69.85%);
            padding: rem(40) 0 0;
        }
        &__title{
            font-size: rem(23);
            line-height: rem(26);
            text-align: center;
            margin-bottom: rem(20);
            @media (min-width: 768px) {
                font-size: rem(40);
                line-height: rem(47);
            }
        }
        &__img{
            max-width: rem(490);
            max-height: rem(300);
            margin:0 auto;
            img{
                width: 100%;
                margin:0 auto;
            }
        }
        &__partners{
            .title--h2{
                font-size: rem(19);
                color: #000203;
                padding: rem(15) rem(25);
            }
            .s-partners__wrapper{
                background: #000203;
                opacity: 0.8;
            }
        }
    }
}
