$cubic-bezier: cubic-bezier(0.2, 0.3, 0.25, 0.9);
$body-dark: #333e48;

.menu {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;

    .submenu {
        display: none;
        align-items: flex-start;
        position: absolute;
        z-index: 99;
        top: 100%;
        left: 0;
        width: 100%;
        min-height: rem(509);
        padding: rem(20) rem(70);
        animation: show 0.4s ease-in-out;
        background: $grey_light;

        @media screen and (max-width: 991px) {
            display: flex;
            flex-direction: column;
            position: fixed;
            z-index: 100;
            top: rem(64);
            left: 0;
            height: calc(100vh - rem(64));
            min-height: calc(100vh - rem(64));
            padding: 0;
            transform: translateX(100%);
            transition: all 0.6s ease-in-out;
            color: #9a9ea9;
            background: $black;
            opacity: 0;
        }

        &:before {
            content: "";
            position: absolute;
            z-index: 2;
            top: 0;
            left: 0;
            width: rem(470);
            height: 100%;
            background: $grey_light;

            @media screen and (max-width: 991px) {
                display: none;
            }
        }

        &__items {
            @media screen and (max-width: 991px) {
                width: 100%;
                padding: rem(30) 0;
            }
        }

        &__back {
            display: none;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            padding: rem(22) rem(70) rem(22) rem(17);
            color: $white;
            background: #0f1113;

            @media screen and (max-width: 991px) {
                display: flex;
            }

            &-icon {
                svg {
                    fill: $grey_dark;
                }
            }
        }

        &__item {
            @include tr("all");
            position: relative;
            z-index: 2;
            min-width: rem(400);
            margin-right: rem(30);

            @media screen and (max-width: 991px) {
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: flex-start;
                min-width: 0;
                margin-right: 0;
                margin-bottom: rem(20);
            }

            &:before {
                content: "";
                display: none;
                position: absolute;
                top: 0;
                left: 0;
                width: rem(2);
                height: 100%;
                background: $red;

                @media screen and (max-width: 991px) {
                    display: none;
                }
            }

            &:hover {
                @media screen and (min-width: 991px) {
                    background: #7d7e7f;
                }
                &:before {
                    display: block;

                    @media screen and (max-width: 991px) {
                        display: none;
                    }
                }
            }

            &-top {
                display: flex;
                align-items: center;
                width: 100%;
                padding: rem(13) rem(15) rem(13) 36px;

                @media screen and (max-width: 991px) {
                    justify-content: flex-end;
                    padding: 0 rem(17);
                    padding-bottom: rem(20);
                }
            }

            &.active {
                color: $white;
                background: rgba($grey_light_more, 0.7);

                @media screen and (max-width: 991px) {
                    color: inherit;
                    background: none;
                }

                &:before {
                    display: block;
                }

                &:hover {
                    @media screen and (min-width: 991px) {
                        background: rgba($grey_light_more, 0.7);
                    }
                    &:before {
                        @media screen and (min-width: 991px) {
                            display: block;
                        }
                    }
                }

                .submenu {
                    &__item {
                        &-icon {
                            svg {
                                fill: $red;
                            }
                        }
                    }
                }
            }

            &-link {
                display: flex;
                align-items: center;
                justify-content: space-between;
                width: calc(100% - 10px);
                color: $grey_light_more_2;
                @media screen and (max-width: 991px) {
                    display: block;
                    width: 100%;
                    padding: 0;
                    font-size: rem(17);
                    text-align: right;
                    margin-right: rem(41);
                }
            }

            &-icon {
                @include tr("all");
                font-size: rem(10);
                @media screen and (max-width: 991px) {
                    transform: rotate(90deg);
                    font-size: rem(12);
                }
                svg {
                    @include tr("all");
                    fill: $white;

                    @media screen and (max-width: 991px) {
                        fill: $grey_light;
                    }
                }
            }

            &.open {
                .submenu {
                    &__item {
                        &-icon {
                            transform: rotate(-90deg);

                            svg {
                                fill: $red;
                            }
                        }

                        &-top {
                            border-bottom: rem(1) solid $grey_light;
                        }
                    }
                }

                .submenu {
                    &__list {
                        height: auto;
                        padding-top: rem(20);
                        opacity: 1;
                    }
                }
            }
        }

        &__list {
            @include tr("all");
            align-self: center;
            width: calc(100% - 470px);
            overflow: visible;
            animation: show 0.4s ease-in-out;

            @media screen and (max-width: 991px) {
                width: 100%;
                height: 0;
                overflow: hidden;
                opacity: 0;
            }

            .swiper-slide {
                max-width: rem(500);
                height: rem(355);
                filter: drop-shadow(0 rem(9) 11px rgba(51, 51, 51, 0.48));
                border-radius: rem(5);
                color: $white;
                background: $black;

                &-active {
                    position: relative;

                    &:after {
                        content: "";
                        display: block;
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        width: calc(100% + 5%);
                        height: calc(100% + 5%);
                        border: rem(2) solid $grey_light_more;
                        transform: translate(-50%, -50%);
                        filter: blur(rem(2));
                        border-radius: rem(10);
                    }
                }
            }
        }

        &__slide {
            display: flex;
            align-items: flex-end;
            position: relative;
            // overflow: hidden;
            @media screen and (max-width: 991px) {
                text-align: right;
                margin-bottom: rem(15);
                padding-left: rem(17);
                padding-right: rem(70);
            }
            &-img {
                display: flex;
                position: relative;
                width: 50%;
                min-height: 100%;
                margin-left: auto;

                @media screen and (max-width: 991px) {
                    display: none;
                }

                img {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    min-height: 100%;
                    object-fit: cover;
                    object-position: left;
                }
            }

            &-info {
                width: 50%;
                padding: rem(44);

                @media screen and (max-width: 991px) {
                    width: 100%;
                    padding: 0;
                }
            }

            &-text {
                font-size: rem(22);
                line-height: 1.2;
                font-weight: 900;
                margin-bottom: rem(34);

                @media screen and (max-width: 991px) {
                    font-size: rem(17);
                    font-weight: 500;
                    margin-bottom: 0;
                }
            }

            &-watch {
                display: flex;
                align-items: center;
                color: $grey_light;
                @media screen and (max-width: 991px) {
                    display: none;
                }
                &--icon {
                    font-size: rem(12);
                    line-height: 1;
                    margin-right: rem(12);
                    svg {
                        fill: $grey_light;
                    }
                }

                &--text {
                    font-size: rem(15);
                    line-height: 1;
                    font-weight: 500;
                }
            }
        }
    }

    &__items {
        display: flex;
        align-items: center;

        @media screen and (max-width: 991px) {
            width: 100%;
            padding: rem(17) 0;
        }
    }

    &__item {
        display: flex;
        justify-content: center;
        margin-right: rem(55);
        padding: rem(9) 0;

        @media screen and (max-width: 1439px) {
            margin-right: rem(34);
        }

        @media screen and (max-width: 991px) {
            width: 100%;
            margin-right: 0;
            margin-bottom: rem(31);
            padding: 0 rem(20);
        }

        &:hover {
            .submenu {
                @media screen and (min-width: 991px) {
                    display: flex;
                }
            }
        }

        &:last-child {
            margin-right: 0;
        }

        &-link {
            display: inline-flex;
            position: relative;
            margin: 0;
            border: 0;
            padding: rem(4);
            transition: all 0.2s $cubic-bezier 0s;
            font-size: rem(15);
            line-height: 1.4;
            text-decoration: none;
            white-space: nowrap;
            background-color: transparent;
            cursor: pointer;
            color: $grey_light_more_2;

            @media screen and (max-width: 1439px) {
                font-size: rem(13);
            }

            @media screen and (max-width: 991px) {
                display: flex;
                align-items: center;
                width: 100%;
                font-size: rem(17);
                line-height: 1;
                color: #9a9ea9;
            }

            // The Magic! Animate Border Drawing on Hover
            &:hover {
                transition: color 0.2s $cubic-bezier 0s;
                opacity: 1;
                outline: 0;
                // color: $red !important;
                text-decoration: none;

                @media screen and (max-width: 991px) {
                    color: $white !important;

                    &::after {
                        display: none;
                    }

                    svg {
                        fill: $red;
                    }
                }

                /* Positioning */
                &::after {
                    width: 100%;
                    transition: width 0.2s $cubic-bezier 0s,
                        background 0.2s $cubic-bezier 0s;
                }
            }

            &:active {
                color: $red;
            }

            // The bottom border
            &::after {
                content: "";
                display: block;
                position: absolute;
                bottom: 0;
                left: 50%;
                width: 80%;
                width: 0;
                height: rem(1);
                transform: translateX(-50%);
                transition: width 0.2s $cubic-bezier 0.4s,
                    background 0.2s $cubic-bezier 0s;
                background: $red;
            }

            &.active {
                // pointer-events: none;
                &:after {
                    width: 100%;
                    @media screen and (max-width: 991px) {
                        display: none;
                    }
                }
            }
        }

        &-text {
            @media screen and (max-width: 991px) {
                margin: 0 auto;
                margin-right: rem(41);
            }
        }

        &-icon {
            display: none;

            @media screen and (max-width: 991px) {
                display: flex;
                align-items: center;
                font-size: rem(12);
                line-height: 1;
            }
        }

        &.open {
            .submenu {
                opacity: 1;
                transform: translateX(0);
            }
        }
    }
}
