.s-feedback {
    padding: rem(50) 0;

    @media (min-width: 1600px) {
        padding: rem(79) 0;
    }

    @media (max-width: 600px) {
        padding: 0 0 50px;
    }

    &__title {
        margin-bottom: rem(45);
        font-size: rem(82);
        font-weight: 400;
        line-height: rem(96);
        color: #ebebeb1a;
        letter-spacing: 0.78em;
    }

    &__content {
        display: flex;
        justify-content: center;
        flex-direction: column;
        gap: rem(20);

        @media (min-width: 768px) {
            gap: rem(60);
        }

        @media (min-width: 1024px) {
            gap: rem(75);
        }

        @media (min-width: 1600px) {
            align-items: center;
            justify-content: space-between;
            flex-direction: row;
            flex-wrap: nowrap;
        }

        &-top {
            @media (min-width: 768px) {
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }

        &-bottom {
            @media (max-width: 1023px) {
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }

    &__item {
        &-text {
            margin-bottom: rem(20);
            font-size: rem(15);
            font-weight: 400;
            line-height: rem(45);
            text-align: center;
            color: $grey_light_more_2;

            @media (min-width: 768px) {
                margin: 0 rem(30) 0 0;
                font-size: rem(19);
                line-height: rem(22);
            }

            @media (min-width: 1024px) {
                margin: 0 rem(110) 0 0;
                font-size: rem(25);
            }

            @media (min-width: 1600px) {
                margin: 0 rem(40) 0 0;
                font-size: rem(38);
                line-height: rem(47);
            }
        }

        &-btn {
            display: flex;
            align-items: center;
            justify-content: center;
            max-width: max-content;
            margin: 0 auto rem(30);
            padding: rem(15) rem(33);
            font-size: rem(12);
            font-weight: 400;
            line-height: 1.2;
            color: $grey_light_more_2;
            border: rem(1) solid $grey_white;
            border-radius: rem(10);
            white-space: nowrap;

            @media (min-width: 768px) {
                margin: 0;
            }

            &:hover {
                border: rem(1) solid $red;
            }
        }

        &-note {
            display: flex;
            align-items: center;
            max-width: rem(550);
            font-size: rem(10);
            font-weight: 300;
            line-height: 1.2;
            color: $grey;

            @media (min-width: 1024px) {
                max-width: rem(730);
            }

            @media (min-width: 1600px) {
                max-width: rem(540);
            }
        }
    }
}
